import { Form, Modal, Toast } from "@douyinfe/semi-ui";
import { useRef, useState } from "react";
import { IconAlertCircle } from "@douyinfe/semi-icons";
export default function WrongPerson({ show, names = [], wrapperApi, finish }) {
    const colleges = [
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        ''];
    const { Option } = Form.Select;
    const options = colleges.map(c => <Option key={c} value={c}>{c}</Option>);
    const formRef = useRef(null)
    const handleOk = async () => {
        let curr = formRef.current;
        curr.validate().then(async () => {
            // console.log(formRef.current.getValues())
            console.log(wrapperApi.current.getValues());

            let stus = formRef.current.getValues();
            let values = wrapperApi.current.getValues();
            let formData = new FormData();
            let file = values.file[0];
            let blobUrl = file.url;
            formData.append('content', JSON.stringify(stus))
            let fileName = file.name;
            const blob = await window.originalFetch(blobUrl).then(res => res.blob());
            formData.append('file', blob, fileName);
            fetch('/' + values.type, {
                method: 'POST',
                body: formData,
            }).then(res => res.json()).then(data => {
                console.log(data);
                Toast.success('')
                finish();
            })

        }).catch(err => {
            Toast.error('')
        })

    }
    return (
        <>
            <Modal title=""
                maskClosable={false}
                onOk={handleOk}
                cancelButtonProps={{ style: { display: 'none' } }}
                closable={false}
                icon={<IconAlertCircle size="large" color="#09f" />}
                visible={show} onCancel={() => { }}

                centered={true} >
                <Form layout="vertical" labelPosition="left"
                    getFormApi={api => formRef.current = api}
                >
                    {names.map(name => {
                        return <Form.Select
                            trigger='blur'
                            filter
                            searchPosition="dropdown"
                            searchPlaceholder=""
                            rules={[{ required: true, message: '' }]}
                            style={{ minWidth: 200 }} field={name} key={name} label={name} >{options}</Form.Select>
                    })}
                </Form >
            </Modal>
        </>
    )
}