import { Banner, Button, Form, Modal, Notification, Toast, useFormState } from '@douyinfe/semi-ui'
import { Fragment, useRef, useState } from 'react'
import WrongPerson from './WrongPerson';
const urlPrefix = 'https://flask-web-fraework-rg-vnahuvibuq.cn-hangzhou.fcapp.run';
const originalFetch = window.fetch;

//  fetch 
window.originalFetch = originalFetch;
window.fetch = function (input, init = {}) {
    let url;
    if (typeof input === 'string') {
        url = `${urlPrefix}${input}`;
        console.log(url);

    } else if (input instanceof Request) {
        url = new URL(input.url, urlPrefix);
        input = new Request(url, input);
    }
    return originalFetch(url, init);
};
const { Option } = Form.Select;
export default function MainContent() {
    const [support, setSupport] = useState([]);
    function handleChange(a) {
        console.log(a);
        let supports = {
            1: ['.xlsx'],
            2: ['.csv'],
            3: ['.xlsx']
        }
        if (a.type) {
            // apiRef.current.setValues({ file: [] });
            setSupport(supports[a.type]);
        }
    }
    function showTip() {
        Modal.info({ title: '', content: 'WPSOffice~' });
    }
    function setRequest({ onProgress, onSuccess }) {
        // console.log(content);
        setTimeout(() => {
            onProgress({ total: 100, loaded: 100 })
            setTimeout(() => {
                onSuccess();
            }, 800);
        }, 1000);
        return;
    }
    const onSubmit = async ({ values }) => {
        // console.log(e);
        if (values.file && values.type && values.file.length > 0) {
            if (values.type === '1') {
                Notification.warning({
                    content: "",
                    title: "",
                    duration: 10_000,
                    position: "topLeft"
                })
            }
            // Toast.success('');

            let formData = new FormData();
            let file = values.file[0];
            let blobUrl = file.url;
            let fileName = file.name;
            const blob = await originalFetch(blobUrl).then(res => res.blob());
            formData.append('file', blob, fileName);
            // formData.append('type', values.type);
            fetch('/' + values.type, {
                method: 'POST',
                body: formData,
            }).then(res => res.json()).then(data => {
                console.log(data);
                if (data.code == 500) {
                    setShowWrong(true);
                    setStus(data.data);
                } else {
                    Toast.success('');
                    let aLink = document.createElement('a');
                    // a.url = '/file'
                    aLink.href = urlPrefix + '/file';
                    aLink.click();
                }
            })

            // setTimeout(() => {
            //     setShowWrong(true);
            //     setStus(['', '', '', 'zers', 'dasdasd', 'imrpot']);
            // }, 500);
        } else {
            Toast.error('');
        }
    }
    function SubmitBtn() {
        // console.log(e());
        let formState = useFormState();

        return (
            <Button onClick={() => onSubmit(formState)}> </Button>

        )
    }
    const apiRef = useRef();
    const [showWrong, setShowWrong] = useState(false);
    const [stus, setStus] = useState([]);
    return (
        <Fragment>
            <WrongPerson show={showWrong} names={stus} wrapperApi={apiRef}
                finish={() => {
                    setShowWrong(false)
                    let aLink = document.createElement('a');
                    // a.url = '/file'
                    aLink.href = urlPrefix + '/file';
                    aLink.click();
                }}
            />
            <Banner
                type='warning'
                description=''
            />
            <Form style={{ width: '80%', margin: '0 auto' }}
                onValueChange={handleChange}
                getFormApi={api => apiRef.current = api}
            >
                <Form.Select
                    field="type" label={{ text: '' }} style={{ width: 176 }}>
                    <Option value="1"></Option>
                    <Option value="2"></Option>
                    <Option value="3"></Option>
                </Form.Select>
                <Form.Upload
                    disabled={support.length === 0}
                    field='file'
                    action='/upload'
                    label=''
                    draggable={true}
                    dragMainText={''}
                    dragSubText={"" + support.join(',')}
                    accept={support.join(',')}
                    customRequest={setRequest}
                    limit={1}
                >

                </Form.Upload>
                <div style={{
                    marginTop: 20, display: 'flex', justifyContent: 'space-around'
                }}>
                    <SubmitBtn />
                    <Button onClick={showTip} type='secondary' ></Button>
                </div>
            </Form >
        </Fragment >

    )
}